import { message } from 'antd';
import { extend } from 'umi-request';
import { hostName } from '@/config/urls';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
const codeStatus = {
  0: '',
  1001: '',
  1002: '',
  1003: '',
  1004: '',
  1005: '',
};

export const mainHost = extend({
  prefix: `//${hostName}`,
  timeout: 30000,
  credentials: 'include',
});

