export const useRequestGlobalConfig = {
  formatResult: (res: any) => {
    // 
    if (res.code !== undefined) {
      if (res.code === 0) {
        return res.data;
      }
      if (res.code === 1005) {
        return res;
      }
      message.error(res.msg ?? codeStatus[res.code]);
      return null;
    }
    return res;
  },
};
