import { createSlice } from '@reduxjs/toolkit';

interface TemplateInfos {
  isModalVisible: boolean;
  textToSpeechVisible: boolean; // 
  recordModalVisible: boolean; // 
  audioClipsVisible: boolean; // 
  musicType: string;
  time: number;
  templ_id: number;
  musicItem: any;
}

function getInit(): TemplateInfos {
  return {
    isModalVisible: false,
    textToSpeechVisible: false, // 
    recordModalVisible: false, // 
    audioClipsVisible: false, // 
    musicType: '',
    time: 0,
    templ_id: -3,
    musicItem: null,
  };
}
export const counterSlice = createSlice({
  name: 'musicStatus',
  initialState: getInit(),
  reducers: {
    setMusicStatus(state, action) {
      Object.assign(state, action.payload);
    },
  },
});

const musicStatusReducer = counterSlice.reducer;
const musicStatusAction = counterSlice.actions;
export { musicStatusReducer, musicStatusAction };
