import { createSlice } from '@reduxjs/toolkit';

type VIPType =
  | 0 // VIP
  | 101 // -
  | 102 // 
  | 103 // -
  | 104 //  
  | 301 //  - 
  | 302 //  - 
  | 303 //  
  | 800 // ()
  | 801 // ()
  | 802; // ()

interface State {
  userId: number;
  id: number;
  vip: number;
  userType: 'qq' | '';
  username: string;
  created: string;
  DESIGNER_EDITOR_ASSET_USER: boolean;
  PPT_DESIGNER_USER: boolean;
  DESIGNER_WITHOUT_PAGE_INFO: boolean;
  COPY_TEMPLATE_USER: boolean;
  super_designer: number;
  isNewUser: number;
  avatar: string; // 
  vip_type: VIPType;
  bind_phone: number; //  1- 0-
  downloadInfo: {
    dln_value: number; // 
    today_dln_value: number; // 
    today_vip_dln_value: number; // VIP
    total_dln_value: number; // 
    vip_type: number; // 
    vip_value: number; // vip
    limited_unit?: 'year' | 'month' | 'day' | 'set'; // set
    is_upg: number; // 
    pkg_count: number; // 
  };
  team_id?: string | number;
}

const initialState: State = {
  userId: -1,
  id: -1,
  vip: -1,
  userType: '',
  username: '',
  created: '',
  DESIGNER_EDITOR_ASSET_USER: false,
  PPT_DESIGNER_USER: false,
  DESIGNER_WITHOUT_PAGE_INFO: false,
  COPY_TEMPLATE_USER: false,
  super_designer: 0,
  isNewUser: 0,
  avatar: '',
  vip_type: 0,
  team_id: 0,

  downloadInfo: {
    dln_value: 0, // 
    today_dln_value: 0, // 
    today_vip_dln_value: 0,
    vip_type: 0, // 
    vip_value: 0, // vip
    total_dln_value: 0,
  },
};
export const counterSlice = createSlice({
  name: 'userInfo',
  initialState: {
    ...initialState,
  },
  reducers: {
    setUserInfo: (state, action) => {
      if (action.payload.id > 0) {
        Object.assign(state, action.payload);
      } else {
        Object.assign(state, initialState);
      }
    },
    setDownloadInfo: (state, action) => {
      state.downloadInfo = action.payload;
    },
    setBindPhone(state, action) {
      state.bind_phone = action.payload;
    },
  },
});
const userInfoReducer = counterSlice.reducer;
const userInfoAction = counterSlice.actions;
export { userInfoReducer, userInfoAction };
