let params: Record<string, string> = {};
let urlCache = '';

interface UrlParams {
  renovate_type?: 'cover' | 'new';
  redirect?: 'designer' | 'module' | 'watermark';
  upicId?: string; // ID
  picId?: string; // ID
  blankSize?: string; //  eg:1080*1920
  template_version?: string; //  eg: 'v1'
  resourceId?: string; // gid
}

export default (): UrlParams => {
  const tempStr = document.location.href;
  if (tempStr !== urlCache) {
    urlCache = tempStr;

    let tempArr;

    params = {};

    tempArr = tempStr.split('?');
    tempArr = tempArr[1] ? tempArr[1] : '';
    tempArr = tempArr.split('&');

    tempArr.forEach((item: string) => {
      const [key, value] = item.split('=');
      if (key) {
        params[key] = value;
      }
    });
  }

  return params;
};
